<template>
  <div>
    我是搜索组件
    <input
      type="text"
      v-if="visible"
      :value="value"
      @input="$emit('input', $event.target.value)"
    >
    <button @click="$emit('update:visible', false)">隐藏</button>
  </div>
</template>

<script>
export default {
  model: {
    prop: 'value',
    event: 'input',
  },
  props: {
    value: {
      type: String,
      default: '',
    },
    visible: {
      type: Boolean,
      default: true,
    },
  },
};
</script>
